body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

body {
  background-color: #F8F8F8;
  margin: 0 auto;
}

.App {
  margin: 0 auto;
  width: 70vw;
}






.beerList .beers {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.beers .beerItem {
  list-style: none;
  background: white;
  width: 250px;
  height: 250px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.75);
  margin-bottom: 20px;
  margin-top: 20px;
  margin-left: 15px;
  margin-right: 15px;
}

.beers .beerItem .container {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.beerItem .image img {
  width: 50px;
  height: 150px;
}

.beerItem h1 {
  color: #FECE4B;
  font-size: 23px;
}

.beerItem .beerTag {
  color: lightgrey;
}

.beerList {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}

.beerList button {
  background: none;
  border: 3px solid #FECE4B;
  border-radius: 5px;
  color: #FECE4B;
  display: inline-flex;
  font-size: 10px;
  font-weight: bold;
  margin: 10px;
  padding: 10px;
  text-transform: uppercase;
}
.beerSpecs {
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    align-content: space-around;
}

.beerSpecs img {
    height: 500px;
}

.beerSpecs .content {
    width: 70%;
    color: grey;
}

.content h2 {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 25px;
}

.content .small {
    font-size: 20px;
}

.content hr {
    margin-top: 10px;
    margin-bottom: 10px;
    border: none;
    width: 70px;
    height: 5px;
    background-color: #FECE4B;
}

.content .voltage {
    padding-top: 15px;
    padding-bottom: 15px;
}

.content .voltage span {
    padding-right: 15px;
}

.content .served {
    padding-top: 10px;
    padding-bottom: 10px;
}
